<!--  -->
<template>
  <div>
    <div class="body">
      <ArticleCard
        v-for="(value,key) in curArticles"
        :key="key"
        :articles="curArticles"
        :index="key"
      />
    </div>
    <Paging :sum="sum" :num="num" v-if="sum" @curChange="curChange" />
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import ArticleCard from "../components/ArticleCard";
import Paging from "../components/Paging/Paging";
import { getArticlesList, getTag } from "../service";
export default {
  //import
  components: { ArticleCard, Paging },
  props: {
    id: {}
  },
  data() {
    //
    return {
      //
      num: 5,
      //
      sum: 0,
      //
      cur: Number,
      articles: [
        {
          tags: [],
          title: "",
          subTitle: "",
          description: "",
          context: "",
          createTime: "",
          lastEditTime: "",
          _id: ""
        }
      ],
      curArticles: []
    };
  },
  // data
  computed: {},
  //data
  watch: {
    cur() {
      this.getCurArticles();
    }
  },
  //
  methods: {
    // async fetchArticles() {
    //   if (this.id) {
    //     const res = await this.$http.get(`tags/${this.id}`);
    //     this.articles = res.data[0].articlesList;
    //     console.log(res.data[0].articlesList);
    //   } else {
    //     const res = await this.$http.get("articles/list");
    //     this.articles = res.data;
    //     this.articles.reverse();
    //   }
    //   this.$nextTick(function() {
    //     this.sum = this.articles.length;
    //     if (!this.articles[0]) {
    //       this.$TOAST("");
    //     }
    //   });
    // },
    async fetchArticles() {
      if (this.id) {
        const res = await getTag(this.id);
        this.articles = res[0].articlesList;
      } else {
        const res = await getArticlesList();
        this.articles = res;
        this.articles.reverse();
      }
      this.$nextTick(function() {
        this.sum = this.articles.length;
        if (!this.articles[0]) {
          this.$Alert.info({
            content: ""
          });
          // this.$TOAST("");
        }
      });
    },

    //
    curChange(data) {
      this.cur = data;
    },
    //
    getCurArticles() {
      let end;
      let start = this.num * (this.cur - 1);
      this.cur == Math.ceil(this.sum / this.num)
        ? (end = this.articles.length)
        : (end = this.num * this.cur);
      this.curArticles = this.articles.slice(start, end);
    }
  },
  // - this
  created() {
    this.fetchArticles();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.body {
  height: 101rem;
  width: 92%;
  margin: 0 4%;
  padding: 0.1rem 0.1rem;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  user-select: none;
}
@media screen and (max-width: 768px) {
  .body {
    width: 96%;
    margin: 0 2%;
  }
}
</style>