<!--  -->
<template>
  <div class="page-body" v-if="$route.path==='/'">
    <div class="newer" @click="reduce" :class="{'hide':cur==1}"></div>
    <ul>
      <li
        v-for="(value,key) in list"
        :key="key"
        :class="{'active':cur==list[key]}"
        @click="toPage(list[key])"
      >{{list[key]}}</li>
    </ul>
    <div class="older" @click="add" :class="{'hide':cur==pags}"></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: {
    //
    sum: Number,
    //
    num: Number
  },
  data() {
    //
    return {
      cur: 0,
      pags: Number,
      pagegroup: 5
    };
  },
  // data
  computed: {
    list() {
      let c = this.cur;
      let t = Math.ceil(this.sum / this.num);
      let arr = [];
      if (t < 2) {
        return [1];
      }
      if (t <= 6) {
        for (let i = 1; i <= t; i++) {
          arr.push(i);
        }
        return arr;
      }
      if (c < 3) return [1, 2, 3, "...", t];
      if (c >= 3 && c <= t - 2) return [1, "...", c - 1, c, c + 1, "...", t];
      if (c > t - 2) return [1, "...", t - 2, t - 1, t];
    }
  },
  //data
  watch: {
    cur() {
      this.$emit("curChange", this.cur);
    }
  },
  //
  methods: {
    //
    getPags() {
      this.pags = Math.ceil(this.sum / this.num);
    },
    //
    toPage(index) {
      if (index != "...") {
        this.cur = index;
      } else {
        this.$TOAST("hi");
      }
    },
    //
    add() {
      if (this.cur < this.pags) {
        this.cur++;
      } else {
        this.$TOAST("");
      }
    },
    //
    reduce() {
      if (this.cur > 1) {
        this.cur--;
      } else {
        this.$TOAST("");
      }
    }
  },
  // - this
  created() {
    this.getPags();
    this.cur = 1;
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.page-body {
  width: 92%;
  margin: 2% 4%;
  height: 4rem;
  line-height: 4rem;
  display: flex;
  justify-content: space-between;
  text-align: center;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  font-size: 1.6rem;
}
.newer {
  width: 20%;
  transition: 0.2s;
}
.older {
  width: 20%;
  transition: 0.2s;
}
ul {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}
li {
  width: 10%;
  transition: 0.2s;
}
.active {
  background: rgb(65, 184, 131);
  color: white;
}
.active:hover {
  color: white;
}
li:hover {
  color: rgb(65, 184, 131);
}
.newer:hover {
  color: rgb(65, 184, 131);
}
.older:hover {
  color: rgb(65, 184, 131);
}
.hide {
  color: #888;
}
.hide:hover {
  color: #888;
  text-decoration: line-through;
}
</style>