<!--  -->
<template>
  <div class="card-body">
    <div class="title">{{model[0].name}}</div>
    <div class="avatar">
      <img :src="model[0].avatar" alt />
    </div>
    <div class="word">{{model[0].description}}</div>
    <div class="word">{{model[0].context}}</div>
    <div class="social"></div>
    <SocialCard />
    <div class="footer line"></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import SocialCard from "./SocialCard";
import { getAboutsList } from "../service";
export default {
  //import
  components: { SocialCard },
  data() {
    //
    return {
      model: [
        {
          avatar: "",
          name: "",
          description: "",
          context: ""
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // async fetchCard() {
    //   const res = await this.$http.get("abouts/list");
    //   this.model = res.data;
    // }
    async fetchCard() {
      const res = await getAboutsList();
      this.model = res;
    }
  },
  // - this
  created() {
    this.fetchCard();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style  scoped>
.card-body {
  width: 98%;
  margin: 5rem 1%;
  /* background: blue; */
  padding: 3%;
  box-shadow: 0.5rem 0.5rem 1.8rem #888;
  /* transform: rotate(-5deg); */
  user-select: none;
}
.card-body > * {
  text-align: center;
}
.title {
  font-size: 1.8rem;
  font-weight: bold;
}
.avatar {
  margin: 4rem auto;
  width: 70%;
  transition: 0.5s;
}

img {
  /* border-radius: 50%; */
  border-radius: 1rem;
  transition: 0.5s;
  /* transform: rotate(-10deg); */
}

.word {
  color: #ccc;
  font-size: 1.4rem;
  margin: 0.5rem 0;
}
.footer {
  margin-top: 2rem;
  position: relative;
}
</style>